import { defineStore } from "pinia";
import { useEditionStore } from "./EditionStore";
import uniqid from "uniqid";



const useWireFrameDocumentStore = defineStore({
  id: "WireFrameDocumenStore",
  state: () => {
    return {
      document: {
        id: "1",
        pages: [] as ActivePage[],
        name: "Active Document",
        properties: {
          backgroundColor: "#ffffff",
          width: 210,
          height: 290,
          measurementUnit: "mm",
        },
        styles: [] as style[],
      },
      elements: {} as { [key: string]: Array<ElementInterface> },
      styles: [] as style[],
    };
  },
  getters: {
    getCurrentPage: (state) => () => {
      const editionStore = useEditionStore();
      const actualPage = editionStore.actualPage;
      return state.document.pages.find(
        (currentpage) => actualPage === currentpage.name
      );
    },
    getElementById: (state) => (id: string) => {
      const values = id.split("|");
      const elementId = values[0];
      const page = values[1];
      let finded = state.elements[page]?.find(
        (element) => element.id == elementId
      );
      if (!finded) {
        const editionstore = useEditionStore();
        finded = editionstore.notes.find((note) => note.id == id);
      }
      return finded;
    },
    getElement: (state) => (id: string) => {
      const editionStore = useEditionStore();
      const actualPage = editionStore.actualPage;
      return state.elements[actualPage].find((element) => element.id == id);
    },
    currentTarget: (state) => {
      const editionStore = useEditionStore();

      return () => {
        if (editionStore.targets.length == 0) return;
        return state.elements[editionStore.actualPage].find(
          (element) => element.id == editionStore.targets[0]?.id
        );
      };
    },
  },
  actions: {
    updateWireFrameDocument(wireFrameDocument: WireFrameDocument) {
      this.styles = wireFrameDocument.styles;
      this.document = wireFrameDocument;
      const editionStore = useEditionStore();
      editionStore.goToFirstPage();
      wireFrameDocument.pages.forEach((page) => {
        if (page.elements) {
          this.elements[page.name] = page.elements;
        }
      });
    },
    addBlock(newBlock: ElementInterface, pageName: string) {
      newBlock.properties = newBlock.properties ?? {};
      newBlock.id = newBlock.id.toString();
      newBlock.type = newBlock.type.toUpperCase();
      const page = this.document.pages.find((page) => page.name == pageName);
      if (!page) {
        return;
      }
      if (page.elements === undefined) {
        page.elements = [];
      }
      page.elements = [...page.elements, newBlock];
      this.addElement(newBlock, pageName);
    },
    addPage(newPage: ActivePage) {
      const editionStore = useEditionStore();
      if (!newPage.pagePosition) {
        newPage.pagePosition =
          this.document.pages.reduce((maxPagePosition, page) => {
            return Math.max(maxPagePosition, page.pagePosition);
          }, 0) + 1;
        newPage.name = newPage.pagePosition.toString();
      }
      let elements: ElementInterface[] = [];
      if (newPage.elements != undefined) {
        elements = newPage.elements;
      }
      this.elements[newPage.name] = [];

      elements.forEach((element) => {
        this.addElement(element, newPage.name);
      });
      this.document.pages.push(newPage);
      editionStore.actualPage = newPage.name;
    },
    addElement(newBlock: ElementInterface, pageName: string) {
      newBlock.properties = newBlock.properties ?? {};
      newBlock.size = newBlock.size ?? {};
      newBlock.id = newBlock.id.toString();
      this.elements[pageName].push(newBlock);
    },

    removeBlock(block: ElementInterface) {
      const editionStore = useEditionStore();
      const actualPage = editionStore.actualPage;
      const page = this.document.pages.find(
        (currentPage) => actualPage === currentPage.name
      );

      page.elements = page.elements.filter(
        (currentElement) =>
          block.id !== currentElement.id && block.id !== currentElement.parentId
      );
      this.elements[actualPage] = this.elements[actualPage].filter(
        (currentElement) =>
          block.id !== currentElement.id && block.id !== currentElement.parentId
      );
    },

    addPageWithIndex(index: number) {
      const pageCounter =
        this.document.pages.reduce((maxPagePosition, page) => {
          return Math.max(maxPagePosition, page.pagePosition);
        }, 0) + 1;
      const elements: ElementInterface[] = [];
      const page = {
        id: uniqid(),
        name: pageCounter.toString(),
        pagePosition: pageCounter,
        elements: elements,
      };
      this.elements[page.name] = elements;
      this.document.pages.splice(index, 0, page);
    },
  },
});

export { useWireFrameDocumentStore };
