/* eslint-disable @typescript-eslint/ban-types */
import apiManager from "../api/apiManager";
import type { WireframeEventInterface } from "@/Events/WireframeEvent";

/**
 * @subscriber is an implementation of observer  pattern
 *
 * The subscriber run a function when a specific event is emit
 *
 * You can subscribe an event with his name and a callback function
 *
 * For emit an event, just call publish func with name, you can give a context data too.
 */
const subscriber: subscriberType = {
  list: {},

  publish(eventName, data) {
    if (!subscriber.list[eventName]) return;

    subscriber.list[eventName].forEach((subscriberCallback) =>
      subscriberCallback(data)
    );
  },

  async dispatch(event: WireframeEventInterface) {
    if (!subscriber.list[event.eventName]) return;

    subscriber.list[event.eventName].forEach((subscriberCallback) =>
      subscriberCallback(event)
    );
  },

  subscribe(eventName: string, callback) {
    if (!subscriber.list[eventName]) {
      subscriber.list[eventName] = [];
    }

    subscriber.list[eventName].push(callback);
  },
};

apiManager.addFunction(subscriber.subscribe, "subscribe");

export default subscriber;

type subscriberType = {
  list: list;
  publish(eventName: string, data: object): void;
  dispatch(event: WireframeEventInterface): void;
  subscribe(eventName: string, callback: Function): void;
};

type list = {
  [key: string]: Function[];
};
