<template>
  <div class="pageSelectorWrapper" v-if="!editionStore.flatPlanMode">
    <ActiveIcon
      :width="11"
      :height="11"
      :path="arrowLeftEnd"
      :iconColor="'var(--white)'"
      :isDisable="isFirstPage"
      @click="editionStore.goToFirstPage"
    />
    <ActiveIcon
      :width="11"
      :height="11"
      :path="arrowLeft"
      :iconColor="'var(--white)'"
      :isDisable="isFirstPage"
      @click="editionStore.goToPreviousPage"
    />
    <InputSelect
      :options="wireframeDocumentStore.document.pages.map((page) => page.name)"
      :actualValue="editionStore.actualPage"
      :onChangeCallback="changePage"
    />
    <ActiveIcon
      :width="11"
      :height="11"
      :path="arrowRight"
      :iconColor="'var(--white)'"
      :isDisable="isLastPage"
      @click="editionStore.goToNextPage"
    />
    <ActiveIcon
      :width="11"
      :height="11"
      :path="arrowRightEnd"
      :iconColor="'var(--white)'"
      :isDisable="isLastPage"
      @click="editionStore.goToLastPage"
    />
  </div>
</template>

<script setup lang="ts">
import { useEditionStore } from "@/stores/EditionStore";
import ActiveIcon from "@/DesignSystem/Icons/IconSvg.vue";
import { useWireFrameDocumentStore } from "@/stores/WireFrameDocumenStore";

import arrowLeft from "@/DesignSystem/Icons/List/ArrowLeft";
import arrowLeftEnd from "@/DesignSystem/Icons/List/ArrowLeftEnd";
import arrowRightEnd from "@/DesignSystem/Icons/List/ArrowRightEnd";
import arrowRight from "@/DesignSystem/Icons/List/ArrowRight";
import { computed, watch } from "vue";
import InputSelect from "@/DesignSystem/Form/InputSelect.vue";
import subscriber from "@/Events/subscriber";

const editionStore = useEditionStore();
const changePage = (newValue:string )=>{
  editionStore.actualPage = newValue
}
const wireframeDocumentStore = useWireFrameDocumentStore();
watch(editionStore.getActualPage, (actualPage)=>{
  const pageID = wireframeDocumentStore.document.pages.find((page)=>page.name == actualPage)?.id
  subscriber.publish("PAGE_CHANGE", {pageID})
})

const isFirstPage = computed(
  () =>
    wireframeDocumentStore.document?.pages[0]?.name == editionStore.actualPage
);

const isLastPage = computed(
  () =>
    wireframeDocumentStore.document?.pages.slice(-1)[0]?.name ==
    editionStore.actualPage
);
</script>

<style scoped>
.pageSelectorWrapper {
  display: flex;
  border-radius: 5px;
  padding: 5px;
  gap: 5px;
  align-items: center;
}
</style>
