import { defineStore } from "pinia";
import { useWireFrameDocumentStore } from "./WireFrameDocumenStore";

const useEditionStore = defineStore({
  id: "EditionStore",
  state: () => {
    return {
      draggable: true,
      rotatable: false,
      warpable: false,
      scalable: false,
      resizable: true,
      selectable: true,
      snappable: false,
      zoomMultiplier: 1,
      actionPanel: {},
      previewMode: false,
      actualPage: "",
      targets: [] as (HTMLElement | null)[],
      flatPlanMode: false,
      spreadMode: false,
      notes: [] as Note[],
      deletedPage: [] as ActivePage[],
      showTrash: false,
    };
  },
  getters: {
    getActualPage: (state) => () => {
      return state.actualPage;
    },
    getNotes: (state) => (pageId: string) => {
      return state.notes.filter((currentNote) => {
        return currentNote.pageId === pageId;
      });
    },

  },
  actions: {
    goToFirstPage() {
      const wireFrameDocumentStore = useWireFrameDocumentStore();
      this.actualPage = wireFrameDocumentStore.document?.pages[0]?.name;
    },
    goToLastPage() {
      const wireFrameDocumentStore = useWireFrameDocumentStore();
      this.actualPage =
        wireFrameDocumentStore.document?.pages.slice(-1)[0]?.name;
    },
    goToPreviousPage() {
      const wireFrameDocumentStore = useWireFrameDocumentStore();
      const previousPageIndex =
        wireFrameDocumentStore.document?.pages.findIndex(
          (page) => page.name === this.actualPage
        ) - 1;
      if (previousPageIndex < 0) {
        return;
      }
      this.actualPage =
        wireFrameDocumentStore.document.pages[previousPageIndex].name;
    },

    goToNextPage() {
      const wireFrameDocumentStore = useWireFrameDocumentStore();
      const nextPageIndex =
        wireFrameDocumentStore.document?.pages.findIndex(
          (page) => page.name === this.actualPage
        ) + 1;
      if (nextPageIndex >= wireFrameDocumentStore.document.pages.length) {
        return;
      }
      this.actualPage =
        wireFrameDocumentStore.document.pages[nextPageIndex].name;
    },
    createNote(leftPosition: string, topPosition: string, pageId: string) {
      const newNote = {
        id: Date.now() + "",
        value: "",
        size: {
          width: "40mm",
          height: "20mm",
          left: leftPosition,
          top: topPosition,
        },
        pageId: pageId,
      };
      this.notes = [...this.notes, newNote];
    },
    changeValueNote(noteId: string, newValue: string) {
      const noteFinded = this.notes.find((note) => note.id == noteId);
      if (noteFinded) {
        noteFinded.value = newValue;
      }
    },
    removeNote(noteId: string) {
      this.notes = this.notes.filter((note) => note.id != noteId);
      this.targets = [];
    },
  },
});

export { useEditionStore };
